body {
  background-color: whitesmoke;
}
.root {
  height: 90vh;
}
.wrapper {
  overflow: hidden;
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-areas:
    ' left-box left-box right-box'
    ' left-box left-box right-box';
  grid-template-columns: auto auto auto;

  color: #444;
}
.left-box {
  grid-area: left-box;
  overflow: scroll;
}
::-webkit-scrollbar {
  display: none;
}
.right-box {
  border-left: solid 1px #c8c8d7;
  grid-area: right-box;
}
.clients {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
}
@media (max-width: 400px) {
  .clients {
    grid-template-columns: 1fr;
  }
  .wrapper {
    grid-template-areas:
      ' left-box left-box left-box'
      ' left-box left-box left-box';
  }
}
@media (min-width: 400px) {
  .right-box {
    width: 30vw;
  }
}
